"use client";
import React, { useState } from 'react';
import { motion, useMotionValue, useTransform, PanInfo } from 'framer-motion';
import { ArrowUpRight, ArrowDownRight, CheckCircle, X } from 'lucide-react';

interface SwipeableSignalProps {
    signal: {
        id: number;
        pair: string;
        side: "BUY" | "SELL";
        entry: number;
        sl: number;
        tp: number;
    };
    onApprove: () => void;
    onReject: () => void;
}

export const SwipeableSignal = ({ signal, onApprove, onReject }: SwipeableSignalProps) => {
    const x = useMotionValue(0);
    const background = useTransform(
        x,
        [-150, 0, 150],
        ["rgba(239, 68, 68, 0.2)", "rgba(10, 12, 16, 1)", "rgba(34, 197, 94, 0.2)"]
    );

    const handleDragEnd = (event: MouseEvent | TouchEvent | PointerEvent, info: PanInfo) => {
        if (info.offset.x > 100) {
            onApprove();
        } else if (info.offset.x < -100) {
            onReject();
        }
    };

    return (
        <div className="relative overflow-hidden rounded-2xl">
            {/* Background Actions */}
            <div className="absolute inset-0 flex items-center justify-between px-6">
                <div className="flex items-center gap-2 text-red-400">
                    <X size={24} />
                    <span className="text-sm font-bold">Reject</span>
                </div>
                <div className="flex items-center gap-2 text-green-400">
                    <span className="text-sm font-bold">Execute</span>
                    <CheckCircle size={24} />
                </div>
            </div>

            {/* Swipeable Card */}
            <motion.div
                drag="x"
                dragConstraints={{ left: 0, right: 0 }}
                dragElastic={0.2}
                onDragEnd={handleDragEnd}
                style={{ x, background }}
                className="relative z-10 cursor-grab active:cursor-grabbing rounded-2xl border border-white/10 p-5"
            >
                <div className="flex items-center justify-between mb-4">
                    <div className="flex items-center gap-3">
                        <div className={`h-10 w-10 rounded-full flex items-center justify-center ${signal.side === "BUY" ? "bg-green-500/20 text-green-400" : "bg-red-500/20 text-red-400"}`}>
                            {signal.side === "BUY" ? <ArrowUpRight size={20} /> : <ArrowDownRight size={20} />}
                        </div>
                        <div>
                            <div className="text-lg font-bold text-white">{signal.pair}</div>
                            <div className={`text-xs font-bold ${signal.side === "BUY" ? "text-green-400" : "text-red-400"}`}>
                                {signal.side}
                            </div>
                        </div>
                    </div>
                    <div className="text-right">
                        <div className="text-xs text-gray-500 uppercase tracking-wider mb-1">Entry</div>
                        <div className="text-lg font-mono font-bold text-white">{signal.entry}</div>
                    </div>
                </div>

                <div className="flex gap-4 border-t border-white/5 pt-4">
                    <div className="flex-1 text-center">
                        <div className="text-[9px] font-bold uppercase tracking-wider text-gray-600 mb-1">Stop Loss</div>
                        <div className="font-mono text-sm font-medium text-red-400">{signal.sl}</div>
                    </div>
                    <div className="flex-1 text-center">
                        <div className="text-[9px] font-bold uppercase tracking-wider text-gray-600 mb-1">Take Profit</div>
                        <div className="font-mono text-sm font-medium text-green-400">{signal.tp}</div>
                    </div>
                </div>

                <div className="mt-4 text-center">
                    <div className="text-[10px] text-gray-500 flex items-center justify-center gap-2">
                        <div className="h-1 w-8 bg-gray-700 rounded-full" />
                        <span>Swipe to Execute or Reject</span>
                        <div className="h-1 w-8 bg-gray-700 rounded-full" />
                    </div>
                </div>
            </motion.div>
        </div>
    );
};
