"use client";
import React, { useState } from 'react';
import { Lock, Edit2 } from 'lucide-react';

export const TakeProfitSlider = () => {
    const [locked, setLocked] = useState(true);
    const [tp1, setTp1] = useState(50);
    const [tp2, setTp2] = useState(15);
    const [tp3, setTp3] = useState(35);

    const total = tp1 + tp2 + tp3;

    return (
        <div className="space-y-4 pt-6">
            <div className="flex items-center justify-between">
                <div className="text-xs font-bold uppercase tracking-widest text-gray-500">Take Profit Allocation</div>
                <div className="flex gap-2">
                    <button
                        onClick={() => setLocked(true)}
                        className={`flex items-center gap-1 rounded-md px-2 py-1 text-[10px] font-bold transition ${locked ? 'bg-green-500/10 text-green-400 border border-green-500/20' : 'text-gray-600'}`}
                    >
                        <Lock size={10} /> Locked
                    </button>
                    <button
                        onClick={() => setLocked(false)}
                        className={`flex items-center gap-1 rounded-md px-2 py-1 text-[10px] font-bold transition ${!locked ? 'bg-blue-500/10 text-blue-400 border border-blue-500/20' : 'text-gray-600'}`}
                    >
                        <Edit2 size={10} /> Edit
                    </button>
                </div>
            </div>

            <div className="rounded-2xl border border-white/5 bg-[#0a0c10] p-5 space-y-6">
                {/* TP1 */}
                <div className="space-y-2">
                    <div className="flex justify-between text-xs">
                        <span className="font-bold text-gray-400">TP1</span>
                        <span className="font-bold text-blue-400">{tp1}%</span>
                    </div>
                    <div className="relative h-2 w-full rounded-full bg-gray-800">
                        <div
                            className="absolute left-0 top-0 h-full rounded-full bg-blue-500"
                            style={{ width: `${tp1}%` }}
                        />
                        {!locked && (
                            <input
                                type="range" min="0" max="100" value={tp1}
                                onChange={(e) => setTp1(parseInt(e.target.value))}
                                className="absolute inset-0 w-full opacity-0 cursor-pointer"
                            />
                        )}
                    </div>
                </div>

                {/* TP2 */}
                <div className="space-y-2">
                    <div className="flex justify-between text-xs">
                        <span className="font-bold text-gray-400">TP2</span>
                        <span className="font-bold text-blue-400">{tp2}%</span>
                    </div>
                    <div className="relative h-2 w-full rounded-full bg-gray-800">
                        <div
                            className="absolute left-0 top-0 h-full rounded-full bg-purple-500"
                            style={{ width: `${tp2}%` }}
                        />
                        {!locked && (
                            <input
                                type="range" min="0" max="100" value={tp2}
                                onChange={(e) => setTp2(parseInt(e.target.value))}
                                className="absolute inset-0 w-full opacity-0 cursor-pointer"
                            />
                        )}
                    </div>
                </div>

                {/* TP3 */}
                <div className="space-y-2">
                    <div className="flex justify-between text-xs">
                        <span className="font-bold text-gray-400">TP3</span>
                        <span className="font-bold text-blue-400">{tp3}%</span>
                    </div>
                    <div className="relative h-2 w-full rounded-full bg-gray-800">
                        <div
                            className="absolute left-0 top-0 h-full rounded-full bg-indigo-500"
                            style={{ width: `${tp3}%` }}
                        />
                        {!locked && (
                            <input
                                type="range" min="0" max="100" value={tp3}
                                onChange={(e) => setTp3(parseInt(e.target.value))}
                                className="absolute inset-0 w-full opacity-0 cursor-pointer"
                            />
                        )}
                    </div>
                </div>

                <div className={`mt-4 rounded-xl py-3 text-center text-xs font-bold transition-colors ${total === 100 ? 'bg-green-500/10 text-green-400' : 'bg-red-500/10 text-red-400'}`}>
                    Total: {total}%
                </div>
            </div>
        </div>
    );
};
