"use client";
import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Zap, Shield, Settings2 } from 'lucide-react';

export const AutoCopySettings = () => {
    const [autoCopy, setAutoCopy] = useState(false);
    const [riskLevel, setRiskLevel] = useState<'conservative' | 'balanced' | 'aggressive'>('balanced');

    return (
        <div className="space-y-4">
            <div className="flex items-center gap-2">
                <div className="h-2 w-2 rounded-full bg-blue-500 animate-pulse" />
                <span className="text-xs font-bold uppercase tracking-widest text-blue-400">Execution Mode</span>
            </div>

            <div className="rounded-2xl border border-blue-500/20 bg-[#0a0c10] overflow-hidden shadow-[0_0_20px_-5px_rgba(59,130,246,0.1)]">
                {/* Auto-Copy Toggle */}
                <div className="p-5 border-b border-white/5">
                    <div className="flex items-center justify-between mb-4">
                        <div className="flex items-center gap-3">
                            <div className={`flex h-10 w-10 items-center justify-center rounded-xl transition-colors ${autoCopy ? 'bg-blue-500/20' : 'bg-gray-800'}`}>
                                <Zap size={20} className={autoCopy ? 'text-blue-400 fill-blue-400' : 'text-gray-500'} />
                            </div>
                            <div>
                                <div className="text-sm font-bold text-white">Auto-Copy Mode</div>
                                <div className="text-[10px] text-gray-500">Fully automated execution</div>
                            </div>
                        </div>
                        <button
                            onClick={() => setAutoCopy(!autoCopy)}
                            className={`h-7 w-14 rounded-full transition-all duration-300 ${autoCopy ? 'bg-blue-600 shadow-[0_0_15px_rgba(59,130,246,0.4)]' : 'bg-gray-700'}`}
                        >
                            <motion.div
                                className="h-5 w-5 rounded-full bg-white shadow-md"
                                animate={{ x: autoCopy ? 28 : 4 }}
                                transition={{ type: "spring", stiffness: 500, damping: 30 }}
                            />
                        </button>
                    </div>

                    {autoCopy && (
                        <motion.div
                            initial={{ height: 0, opacity: 0 }}
                            animate={{ height: 'auto', opacity: 1 }}
                            className="space-y-3 pl-13"
                        >
                            <div className="rounded-lg bg-blue-500/10 border border-blue-500/20 p-3 flex items-start gap-2">
                                <Shield size={14} className="text-blue-400 mt-0.5 shrink-0" />
                                <p className="text-[10px] text-blue-300 leading-relaxed">
                                    All signals will be executed automatically with your configured risk parameters. No manual approval required.
                                </p>
                            </div>
                        </motion.div>
                    )}
                </div>

                {/* Manual Approval Mode */}
                <div className="p-5">
                    <div className="flex items-center gap-3">
                        <div className={`flex h-10 w-10 items-center justify-center rounded-xl transition-colors ${!autoCopy ? 'bg-purple-500/20' : 'bg-gray-800'}`}>
                            <Settings2 size={20} className={!autoCopy ? 'text-purple-400' : 'text-gray-500'} />
                        </div>
                        <div>
                            <div className="text-sm font-bold text-white">Manual Approval</div>
                            <div className="text-[10px] text-gray-500">Swipe to execute each signal</div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Risk Level Selector (only when auto-copy is on) */}
            {autoCopy && (
                <motion.div
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="space-y-3"
                >
                    <div className="text-xs font-bold uppercase tracking-widest text-gray-500">Risk Profile</div>
                    <div className="grid grid-cols-3 gap-2">
                        {(['conservative', 'balanced', 'aggressive'] as const).map((level) => (
                            <button
                                key={level}
                                onClick={() => setRiskLevel(level)}
                                className={`rounded-xl border py-3 text-xs font-bold capitalize transition-all ${riskLevel === level
                                        ? 'border-blue-500/50 bg-blue-500/10 text-blue-400 shadow-[0_0_15px_-2px_rgba(59,130,246,0.3)]'
                                        : 'border-white/5 bg-white/5 text-gray-500 hover:bg-white/10'
                                    }`}
                            >
                                {level}
                            </button>
                        ))}
                    </div>
                </motion.div>
            )}
        </div>
    );
};
