"use client";
import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import {
    Home, Trophy, Link as LinkIcon, Settings,
    Bell, ChevronLeft, Plus
} from 'lucide-react';

import { BalanceCard } from '@/components/trading/mobile/BalanceCard';
import { PerformanceStats } from '@/components/trading/mobile/PerformanceStats';
import { TradeProtectionCard } from '@/components/trading/mobile/TradeProtectionCard';
import { TakeProfitSlider } from '@/components/trading/mobile/TakeProfitSlider';
import { AutoCopySettings } from '@/components/trading/mobile/AutoCopySettings';
import { SwipeableSignal } from '@/components/trading/mobile/SwipeableSignal';

const TradePage = () => {
    const [navIndex, setNavIndex] = useState(0);
    const [pendingSignals, setPendingSignals] = useState([
        { id: 1, pair: "XAUUSD", side: "BUY" as const, entry: 2038.50, sl: 2035.00, tp: 2045.00 },
        { id: 2, pair: "US30", side: "SELL" as const, entry: 38500, sl: 38600, tp: 38200 }
    ]);

    const handleApprove = (id: number) => {
        setPendingSignals(prev => prev.filter(s => s.id !== id));
        // Here you would trigger the actual trade execution
    };

    const handleReject = (id: number) => {
        setPendingSignals(prev => prev.filter(s => s.id !== id));
    };

    return (
        <div className="min-h-screen bg-gradient-to-b from-black via-[#050507] to-black text-white p-4 flex justify-center overflow-x-hidden">

            {/* Ambient Background */}
            <div className="fixed top-0 left-1/2 -translate-x-1/2 w-[600px] h-[600px] bg-blue-600/10 rounded-full blur-[150px] -z-10 animate-pulse" />
            <div className="fixed bottom-0 right-0 w-[400px] h-[400px] bg-purple-600/10 rounded-full blur-[120px] -z-10" />

            {/* Mobile Container */}
            <div className="w-full max-w-[430px] relative pb-28">

                {/* Header */}
                <motion.div
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="flex items-center justify-between mb-6 pt-2"
                >
                    <button className="h-10 w-10 rounded-full bg-white/5 border border-white/10 flex items-center justify-center hover:bg-white/10 transition backdrop-blur-sm">
                        <ChevronLeft size={20} />
                    </button>
                    <div className="flex items-center gap-2">
                        <div className="h-2 w-2 rounded-full bg-green-500 animate-pulse shadow-[0_0_10px_2px_rgba(34,197,94,0.4)]" />
                        <span className="text-sm font-black tracking-wider bg-clip-text text-transparent bg-gradient-to-r from-blue-400 to-purple-400">
                            PRIMESYNC
                        </span>
                    </div>
                    <button className="relative h-10 w-10 rounded-full bg-white/5 border border-white/10 flex items-center justify-center hover:bg-white/10 transition backdrop-blur-sm">
                        <Bell size={20} />
                        <div className="absolute top-2 right-2 h-2 w-2 rounded-full bg-red-500 border-2 border-black animate-pulse" />
                    </button>
                </motion.div>

                <div className="space-y-6">
                    {/* Balance & Performance */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.1 }}
                    >
                        <BalanceCard />
                    </motion.div>

                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.2 }}
                    >
                        <PerformanceStats />
                    </motion.div>

                    {/* Execution Mode */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.3 }}
                    >
                        <AutoCopySettings />
                    </motion.div>

                    {/* Pending Signals */}
                    {pendingSignals.length > 0 && (
                        <motion.div
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ delay: 0.4 }}
                            className="space-y-4"
                        >
                            <div className="flex items-center justify-between">
                                <div className="flex items-center gap-2">
                                    <div className="h-2 w-2 rounded-full bg-orange-500 animate-pulse" />
                                    <span className="text-xs font-bold uppercase tracking-widest text-orange-400">Pending Approval</span>
                                </div>
                                <span className="text-xs font-bold text-gray-500">{pendingSignals.length} Signal{pendingSignals.length !== 1 ? 's' : ''}</span>
                            </div>

                            <div className="space-y-3">
                                <AnimatePresence>
                                    {pendingSignals.map((signal) => (
                                        <motion.div
                                            key={signal.id}
                                            initial={{ opacity: 0, scale: 0.95 }}
                                            animate={{ opacity: 1, scale: 1 }}
                                            exit={{ opacity: 0, scale: 0.95, x: 100 }}
                                        >
                                            <SwipeableSignal
                                                signal={signal}
                                                onApprove={() => handleApprove(signal.id)}
                                                onReject={() => handleReject(signal.id)}
                                            />
                                        </motion.div>
                                    ))}
                                </AnimatePresence>
                            </div>
                        </motion.div>
                    )}

                    {/* Trade Protection */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.5 }}
                    >
                        <TradeProtectionCard />
                    </motion.div>

                    {/* TP Allocation */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.6 }}
                    >
                        <TakeProfitSlider />
                    </motion.div>
                </div>

                {/* Bottom Navigation Bar */}
                <motion.div
                    initial={{ opacity: 0, y: 50 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.7 }}
                    className="fixed bottom-6 left-1/2 -translate-x-1/2 w-[calc(100%-2rem)] max-w-[400px] rounded-[2rem] bg-[#0f1115]/95 border border-white/10 backdrop-blur-2xl p-2 shadow-2xl shadow-black/40 z-50"
                >
                    <div className="flex justify-between items-center px-4">
                        <button
                            onClick={() => setNavIndex(0)}
                            className={`relative p-3 rounded-xl transition-all ${navIndex === 0 ? 'text-white' : 'text-gray-500'}`}
                        >
                            {navIndex === 0 && (
                                <motion.div
                                    layoutId="nav-bg"
                                    className="absolute inset-0 bg-blue-600 rounded-xl shadow-lg shadow-blue-500/30"
                                    transition={{ type: "spring", stiffness: 500, damping: 30 }}
                                />
                            )}
                            <Home size={22} className="relative z-10" />
                        </button>
                        <button
                            onClick={() => setNavIndex(1)}
                            className={`relative p-3 rounded-xl transition-all ${navIndex === 1 ? 'text-white' : 'text-gray-500'}`}
                        >
                            {navIndex === 1 && (
                                <motion.div
                                    layoutId="nav-bg"
                                    className="absolute inset-0 bg-blue-600 rounded-xl shadow-lg shadow-blue-500/30"
                                    transition={{ type: "spring", stiffness: 500, damping: 30 }}
                                />
                            )}
                            <Trophy size={22} className="relative z-10" />
                        </button>
                        <button
                            onClick={() => setNavIndex(2)}
                            className={`relative p-3 rounded-xl transition-all ${navIndex === 2 ? 'text-white' : 'text-gray-500'}`}
                        >
                            {navIndex === 2 && (
                                <motion.div
                                    layoutId="nav-bg"
                                    className="absolute inset-0 bg-blue-600 rounded-xl shadow-lg shadow-blue-500/30"
                                    transition={{ type: "spring", stiffness: 500, damping: 30 }}
                                />
                            )}
                            <LinkIcon size={22} className="relative z-10" />
                        </button>
                        <button
                            onClick={() => setNavIndex(3)}
                            className={`relative p-3 rounded-xl transition-all ${navIndex === 3 ? 'text-white' : 'text-gray-500'}`}
                        >
                            {navIndex === 3 && (
                                <motion.div
                                    layoutId="nav-bg"
                                    className="absolute inset-0 bg-blue-600 rounded-xl shadow-lg shadow-blue-500/30"
                                    transition={{ type: "spring", stiffness: 500, damping: 30 }}
                                />
                            )}
                            <Settings size={22} className="relative z-10" />
                        </button>
                    </div>
                </motion.div>

            </div>
        </div>
    );
};

export default TradePage;
