"use client";
import React from 'react';
import { motion } from 'framer-motion';
import { TrendingUp, Target, Zap, Activity } from 'lucide-react';

export const PerformanceStats = () => {
    const stats = [
        { label: "Win Rate", value: "84.2%", icon: Target, color: "green", trend: "+2.1%" },
        { label: "Total Pips", value: "44,127", icon: TrendingUp, color: "blue", trend: "+1,240" },
        { label: "Latency", value: "18ms", icon: Zap, color: "purple", trend: "Optimal" },
        { label: "Active Signals", value: "12", icon: Activity, color: "orange", trend: "Live" }
    ];

    return (
        <div className="space-y-3">
            <div className="flex items-center gap-2">
                <div className="h-2 w-2 rounded-full bg-green-500 animate-pulse shadow-[0_0_10px_2px_rgba(34,197,94,0.4)]" />
                <span className="text-xs font-bold uppercase tracking-widest text-gray-500">Performance</span>
            </div>

            <div className="grid grid-cols-2 gap-3">
                {stats.map((stat, i) => {
                    const Icon = stat.icon;
                    const colorMap = {
                        green: { bg: 'bg-green-500/10', text: 'text-green-400', border: 'border-green-500/20' },
                        blue: { bg: 'bg-blue-500/10', text: 'text-blue-400', border: 'border-blue-500/20' },
                        purple: { bg: 'bg-purple-500/10', text: 'text-purple-400', border: 'border-purple-500/20' },
                        orange: { bg: 'bg-orange-500/10', text: 'text-orange-400', border: 'border-orange-500/20' }
                    };
                    const colors = colorMap[stat.color as keyof typeof colorMap];

                    return (
                        <motion.div
                            key={stat.label}
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ delay: i * 0.1 }}
                            className={`relative overflow-hidden rounded-2xl border ${colors.border} bg-[#0a0c10] p-4 hover:bg-white/[0.02] transition`}
                        >
                            <div className={`absolute -right-2 -top-2 h-16 w-16 ${colors.bg} rounded-full blur-2xl opacity-50`} />

                            <div className="relative z-10">
                                <div className="mb-3 flex items-center justify-between">
                                    <div className={`flex h-8 w-8 items-center justify-center rounded-lg ${colors.bg}`}>
                                        <Icon size={16} className={colors.text} />
                                    </div>
                                    <span className={`text-[9px] font-bold uppercase tracking-wider ${colors.text}`}>
                                        {stat.trend}
                                    </span>
                                </div>

                                <div className="text-2xl font-black text-white mb-0.5">{stat.value}</div>
                                <div className="text-[10px] font-medium text-gray-500 uppercase tracking-wider">{stat.label}</div>
                            </div>
                        </motion.div>
                    );
                })}
            </div>
        </div>
    );
};
