"use client";
import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Shield, Save, TrendingUp } from 'lucide-react';

export const TradeProtectionCard = () => {
    const [autoBreakeven, setAutoBreakeven] = useState(true);
    const [aiTrail, setAiTrail] = useState(false);
    const [tpTrigger, setTpTrigger] = useState<1 | 2 | 3>(2);

    return (
        <div className="space-y-4">
            <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                    <div className="h-2 w-2 rounded-full bg-blue-500 animate-pulse" />
                    <span className="text-xs font-bold uppercase tracking-widest text-blue-400">Trade Protection</span>
                </div>
                <button className="flex items-center gap-1 rounded-lg bg-[#1a1f2e] px-3 py-1.5 text-xs text-green-400 border border-green-500/20 hover:bg-green-500/10 transition">
                    <Save size={12} />
                    Save
                </button>
            </div>

            <div className="overflow-hidden rounded-2xl border border-blue-500/20 bg-[#0a0c10] shadow-[0_0_20px_-5px_rgba(59,130,246,0.1)]">
                {/* Auto Breakeven Section */}
                <div className="border-b border-white/5 p-5">
                    <div className="mb-4 flex items-center justify-between">
                        <div className="flex items-center gap-3">
                            <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-green-500/10 text-green-400">
                                <Shield size={16} />
                            </div>
                            <div>
                                <div className="text-sm font-bold text-green-400">Auto Breakeven</div>
                                <div className="text-[10px] text-gray-500">Move SL to entry when target is hit</div>
                            </div>
                        </div>
                        <button
                            onClick={() => setAutoBreakeven(!autoBreakeven)}
                            className={`h-6 w-11 rounded-full transition-colors duration-300 ${autoBreakeven ? 'bg-blue-600' : 'bg-gray-700'}`}
                        >
                            <div className={`h-4 w-4 transform rounded-full bg-white shadow-md transition-transform duration-300 ${autoBreakeven ? 'translate-x-6' : 'translate-x-1'}`} />
                        </button>
                    </div>

                    {autoBreakeven && (
                        <motion.div
                            initial={{ height: 0, opacity: 0 }}
                            animate={{ height: 'auto', opacity: 1 }}
                            className="space-y-3 pl-11"
                        >
                            <div className="text-[10px] font-bold uppercase tracking-wider text-gray-600">Breakeven Trigger</div>
                            <div className="flex gap-2">
                                {[1, 2, 3].map((tp) => (
                                    <button
                                        key={tp}
                                        onClick={() => setTpTrigger(tp as 1 | 2 | 3)}
                                        className={`flex-1 rounded-lg border py-2 text-xs font-bold transition-all ${tpTrigger === tp
                                                ? 'border-green-500/50 bg-green-500/10 text-green-400 shadow-[0_0_10px_-2px_rgba(74,222,128,0.2)]'
                                                : 'border-white/5 bg-white/5 text-gray-500 hover:bg-white/10'
                                            }`}
                                    >
                                        TP{tp}
                                    </button>
                                ))}
                            </div>
                            <p className="text-[10px] text-gray-500 leading-relaxed">
                                {tpTrigger === 1 && "Conservative. Secures risk quickly at the first target."}
                                {tpTrigger === 2 && "Balanced. Lets the trade breathe before locking in at TP2."}
                                {tpTrigger === 3 && "Aggressive. Maximizes runner potential by waiting for TP3."}
                            </p>
                        </motion.div>
                    )}
                </div>

                {/* AI Trail Section */}
                <div className="p-5">
                    <div className="flex items-center justify-between">
                        <div className="flex items-center gap-3">
                            <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-purple-500/10 text-purple-400">
                                <TrendingUp size={16} />
                            </div>
                            <div>
                                <div className="text-sm font-bold text-white">AI Trail</div>
                                <div className="text-[10px] text-gray-500">Structure-based trailing stop</div>
                            </div>
                        </div>
                        <button
                            onClick={() => setAiTrail(!aiTrail)}
                            className={`h-6 w-11 rounded-full transition-colors duration-300 ${aiTrail ? 'bg-purple-600' : 'bg-gray-700'}`}
                        >
                            <div className={`h-4 w-4 transform rounded-full bg-white shadow-md transition-transform duration-300 ${aiTrail ? 'translate-x-6' : 'translate-x-1'}`} />
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};
